
.top{
    position: absolute;
    top: 0;
    left: 0;
    height: 6%;
    box-shadow: gainsboro 0px 5px 5px;
    
    display: flex;
    align-items: center;
    flex-direction: row ;
    width: 100%;
    z-index: 10;
    background: white;
}
.img{
    margin-left: 25px;
    position: absolute;
    height: 90%;
    pointer-events: none

}
body{
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */
}
.snifer{
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border: 3px solid royalblue;
    border-radius: 10px;
    padding: 50px;
    padding-right: 10px;

    box-shadow: 15px 15px 10px gainsboro;

}


.blocker{
    position: absolute;
    top: 0;
    left: 0;
    height: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 50px;
    color: white;
    visibility: hidden;
}

@media screen and (max-width: 1000px)  {
    .blocker {
        z-index: 50;
        visibility:visible;
        background-color: royalblue;

    }
  }
  