.nó {
    position: absolute;
    width: auto;
    height: auto;

    word-break: break-all;
    color: royalblue;
    text-align: center;
    z-index: 7;
    background-color: white;
    border-radius: 4px;
    -webkit-user-select: none;
    /* Safari */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* IE10+/Edge */
    user-select: none;
    /* Standard */
    line-height: 26px;


    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;

    border: 1px solid royalblue;


}
.drag{
    width: 100%;
    min-height: 20px;
    background-color: cornflowerblue;
    height: 10%;
    max-height: 50px;
    color: white;
    
}
.nó_text {
    
    min-height: 30px;
    max-width: 100px;
    border-radius: 4px;
    z-index: 15;
    border: 0;
    visibility: visible;
    width: 100%;
    height: 100%;
    margin: 0;
    word-wrap: break-word;

}
.link_no {
    
    min-height: 30px;
    max-width: 200px;
    max-height: 70;
    overflow-y: hidden;
    border-radius: 4px;
    z-index: 15;
    border: 0;
    visibility: visible;
    width: 100%;
    height: 100%;
    margin: 0;
    width: 100%;
    height: 100%;
    word-wrap: break-word;

}
.img_No {
    height: 130px;
    width: 200px;
}