
.toolbar_main{
    z-index: 3;
    background-color: white;
    position: absolute;
    width:50px;
    height: 94%;
    bottom: 0px;
    left:  0px;
    border-right: 2px royalblue solid;
}
.toolbar_container{
    position:absolute ;
    width:100%  ;
    height:auto;
    margin-top: 20px;

    display: flex;
    flex-direction: column;
    border: 0px black solid;
}
.toolbar_botão{
    width:30px;
    height: 30px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-self: center;
    padding: 3px;
    border: 3px white solid;
}
.toolbar_botão:hover{
    border: 3px royalblue solid;
    
}
.toolbar_botão:active {
    border: 3px solid rgb(249, 146, 69);
}
.toolbar_img{
    left: 50%;
    top: 50%;
    pointer-events: none;
}